html, body {
  overflow-x: hidden;
  max-width: 100vw;
  font-family: 'Ubuntu', sans-serif;
  color: #676767; }

body {
  font-size: 1rem; }
  @media (min-width: 650px) {
    body {
      font-size: 1.125rem; } }
  @media (min-width: 1000px) {
    body {
      font-size: 1.25rem; } }

strong {
  font-weight: bold; }

.btn-reset {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background: none;
  cursor: pointer; }
  .btn-reset::-moz-focus-inner {
    border: none;
    padding: 0; }

.btn--arrow {
  width: 30px;
  height: 30px; }
  .btn--arrow:before, .btn--arrow:after {
    content: '';
    display: inline-block;
    width: 15px;
    height: 2px;
    background: #676767;
    border-radius: 8px;
    transition: transform 0.2s, background-color 0.2s; }
  .btn--arrow:before {
    transform: translate(19%, -7px) rotate(45deg); }
  .btn--arrow:after {
    transform: translate(-19%, -7px) rotate(-45deg); }
  .btn--arrow.active:before {
    transform: translate(19%, -7px) rotate(-45deg); }
  .btn--arrow.active:after {
    transform: translate(-19%, -7px) rotate(45deg); }

img {
  vertical-align: top; }

a {
  color: inherit;
  text-decoration: none;
  transition: opacity 0.2s, background-color 0.2s; }
  a:hover {
    opacity: 0.8; }
  a.with-icon {
    display: inline-block;
    opacity: 1; }
    a.with-icon * {
      vertical-align: middle; }
    a.with-icon svg, a.with-icon img {
      margin-right: 10px;
      transition: opacity 0.2s, fill 0.3s; }
    a.with-icon svg * {
      transition: opacity 0.2s, fill 0.3s; }
    a.with-icon span {
      transition: opacity 0.2s; }
    a.with-icon:hover span {
      opacity: 0.7; }
    a.with-icon.not-hover span {
      opacity: 1; }

.normalize {
  margin: 0;
  padding: 0;
  list-style: none; }

.container {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px; }
  @media (min-width: 1821px) {
