.container-block {
  max-width: 1820px;
  padding: 0 20px;
  margin: 0 auto; }

.input {
  border: 1px solid #676767;
  border-radius: 50px;
  padding: 21px 45px; }
  .input--icon {
    display: flex;
    align-items: center; }
    .input--icon input {
