input.input, .input input {
  color: #676767; }
  input.input.placeholder, .input input.placeholder {
    opacity: 1;
    color: #676767; }
  input.input:-moz-placeholder, .input input:-moz-placeholder {
    opacity: 1;
    color: #676767; }
  input.input::-moz-placeholder, .input input::-moz-placeholder {
    opacity: 1;
    color: #676767; }
  input.input:-ms-input-placeholder, .input input:-ms-input-placeholder {
    opacity: 1;
    color: #676767; }
  input.input::-webkit-input-placeholder, .input input::-webkit-input-placeholder {
