@import 'normalize';


//
//base
//
@mixin button-reset {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background: none;
  cursor: pointer;
  &::-moz-focus-inner  {
    border: none;
    padding: 0;
  }
}
@mixin input-placeholder {
  &.placeholder { @content; }
  &:-moz-placeholder { @content; }
  &::-moz-placeholder { @content; }
  &:-ms-input-placeholder { @content; }
  &::-webkit-input-placeholder { @content; }
}

$color-purple: #FF008C;
$color-black: #313131;
$color-grey: #676767;
$color-blue: #01A7D7;

$media-sm: 650px;
$media-md: 1000px;

*, *:after, *:before {
  box-sizing: border-box;
  -webkit-appearance: none;
  appearance: none;
  outline: 0;
  /* for Internet Explorer */
  background: none;
}

html, body  {
  overflow-x: hidden;
  max-width: 100vw;
  font-family: 'Ubuntu', sans-serif;
  color: $color-grey;
}
body {
  font-size: (16rem/16);
  @media (min-width: $media-sm) {
    font-size: (18rem/16);    
  }
  @media (min-width: $media-md) {
    font-size: (20rem/16);   
  }
}
strong {
  font-weight: bold;
}
.btn-reset {
  @include button-reset;
}
.btn--arrow {
  width: 30px;
  height: 30px;
  &:before, &:after {
    content: '';
    display: inline-block;
    width: 15px;
    height: 2px;
    background: $color-grey;
    border-radius: 8px;
    transition: transform 0.2s, background-color 0.2s;
  }
  &:before {
    transform: translate(19%, -7px) rotate(45deg);
  }
  &:after {
    transform: translate(-19%, -7px) rotate(-45deg);
  }
  &.active {
    &:before {
      transform: translate(19%, -7px) rotate(-45deg);
    }
    &:after {
      transform: translate(-19%, -7px) rotate(45deg);
    }
  }
}
img {
  vertical-align: top;
}
a {
  color: inherit;
  text-decoration: none;
  transition: opacity 0.2s, background-color 0.2s;
  &:hover {
    opacity: 0.8;
  }
  &.with-icon {
    display: inline-block;
    opacity: 1;
    * {
      vertical-align: middle;
    }
    svg, img {
      margin-right: 10px;
      transition: opacity 0.2s, fill 0.3s;
    }
    svg * {
      transition: opacity 0.2s, fill 0.3s;
    }
    span {
      transition: opacity 0.2s;
    }
    &:hover span{
      opacity: 0.7;
    }
    &.not-hover span {
      opacity: 1;
    }
  }
}
.normalize {
  margin: 0;
  padding: 0;
  list-style: none;
}
.container {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  @media (min-width: 1821px) {
    padding-right: calc(50% - 890px);
    padding-left: calc(50% - 890px);
  }
}
.container-block {
  max-width: 1820px;
  padding: 0 20px;
  margin: 0 auto;
}
.input {
  border: 1px solid #676767;
  border-radius: 50px;
  padding: 21px 45px;
  &--icon {
    display: flex;
    align-items: center;
    input {
      background: none;
      border: 0;
      width: auto;
      flex: 1 1 auto;
      padding: 0;
      width: 0%;
      min-width: auto;
    }
    .icon {
      margin-left: 15px;
      flex: 0 0 auto;
    }
    button {
      display: flex;
      align-items: center;
    }
  }
  &--search {
    padding: 11px 23px 11px 30px;
  }
}
input.input, .input input {
  color: $color-grey;
  @include input-placeholder {
    opacity: 1;
    color: $color-grey;
  }
} 

//
// Header
//
.main-header {
  position: relative;
  &__top {
    background: $color-blue;
    color: #ffffff;
    font-size: (16rem/16);
    padding-top: 45px;    
    padding-bottom: 15px;
    transform: scaleY(0);
    transform-origin: 50% 0%;
    transition: transform 0.2s;
    position: absolute;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 5;
    @media (min-width: $media-sm) {
      font-size: (18rem/16);
    }
    &.active {
      transform: scaleY(1);
    }
    @media (min-width: 600px) {
      position: relative;
      transform: scaleY(1);
      padding-top: 15px;
    }
    &__ul {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      max-width: 100%;
      @media (min-width: 1000px) {
        flex-direction: row;
      }
      &__li {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        max-width: 100%;
        @media (min-width: 1000px) {
          flex: 1 1 0%;
          display: block;
        }
        a {
          margin: 5px 10px;
          text-align: center;
          @media (min-width: 1000px) {
            text-align: inherit;
            margin: 5px 0;
          }
        }
      }
      &__li--main {
        display: none;
        @media (min-width: 1000px) {
          display: block;
          padding: 0 20px;
          white-space: nowrap;
          flex: 0 0 auto;
        }
      }
      &__li--left {
        text-align: center;
        margin: 0px -10px;
        @media (min-width: 1000px) {
          text-align: left;
          padding-right: 20px;
          margin: 0;
        }
        a:first-child {
          @media (min-width: 1000px) {
            margin-right: 20px;
          }
          @media (min-width: 1600px) {
            margin-right: calc((100vw - 1560px)/2);
          }
          @media (min-width: 1674px) {
            margin-right: 57px;
          }
        }
      }
      &__li--right {
        text-align: center;
        @media (min-width: 1000px) {
          padding-left: 20px;
          text-align: right;
        }
        a:last-child {
          @media (min-width: 1000px) {
            margin-left: 40px;
          }
          @media (min-width: 1600px) {
            margin-left: calc((100vw - 1520px)/2);
          }
          @media (min-width: 1740px) {
            margin-left: 110px;
          }
        }
      }
    }
    &__mobile-menu {
      position: absolute;
      top: 10px;
      left: 50%;
      margin-left: -15px;
      z-index: 10;
      &:before, &:after {
        background: $color-grey;
      }
      &.active {
        &:before, &:after {
          background: #fff;
        }
      }
      @media (min-width: 600px) {
        display: none;
      }
    }
  }
  &__bottom {
    padding-top: 45px;
    padding-bottom: 15px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
    @media (min-width: 850px) {
      justify-content: center;
      padding-top: 20px;
      padding-bottom: 20px;
    }
    @media (min-width: 1500px) {
      padding-top: 35px;
      padding-bottom: 35px;
    }
    &__logo {
      flex: 0 0 auto;
      margin: 15px 0;
      transform: translate(0, 5px);
      display: block;
      width: 180px;
      svg {
        width: 100%;
      }
      @media (min-width: 850px) {
        margin: 15px 46px 15px 0;
        flex: 0 0 auto;
        width: 240px;
      }
      @media (min-width: 1500px) {
        margin: 15px 0;
      }
    }
    &__nav {
      display: none;
      white-space: nowrap;
      @media (min-width: 850px) {
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex: 1 1 auto; // Важно оставлять auto и не ставить никакую максимальную ширину для IE11, если необходимо ограничить, то надо создать блок-обертку 
        padding: 0 20px;
        margin: 15px 0;
        order: 1;
        flex-wrap: wrap;
        width: 100%;
      }
      @media (min-width: 1500px) {
        order: 0;
        width: auto;
      }
      a {
        margin: 0 10px;
      }
    }
    &__right {
      display: flex;
      align-items: center;
      margin: 15px 0;
      width: 100%;
      flex-direction: column;
      flex-basis: auto;
      @media (min-width: 550px) {
        justify-content: space-between;
        flex-wrap: wrap;
        flex-direction: row;
      }
      @media (min-width: 850px) {
        width: auto;
      }
      .input--search {
        width: 272px;
        flex: 0 0 auto;
      }
      &__basket {
        margin-top: 20px;
        @media (min-width: 550px) {
          margin-top: 0;
          margin-left: 46px;
        }
        .price, .basket{
          vertical-align: middle;
        }
        .basket {
          width: 49px;
          height: 49px;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          border-radius: 50%;
          background: $color-purple;
          margin-left: 9px;
        }
      }
    }
    &__mobile {
      position: relative;
      @media (min-width: 850px) {
        display: none;
      }
    }
    &__mobile-menu {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      position: absolute;
      background: $color-blue;
      color: #ffffff;
      padding: 20px;
      width: 100vw;
      max-width: 320px;
      bottom: 0;
      right: -20px;
      opacity: 0;
      visibility: hidden;
      transform: translate(0, 125%);
      transition: transform 0.3s, opacity 0.3s, visibility 0s 0.3s;
      @media (min-width: 320px) {
        right: calc(50vw - 180px);
      }
      @media (min-width: 360px) {
        right: 0;
      }
      &.active {
        transition: transform 0.3s, opacity 0.3s, visibility 0s 0s;
        visibility: visible;
        opacity: 1;
        transform: translate(0, 110%);
      }
    }
    &__mobile-button {
      display: block;
      padding: 9px 5px;
      box-sizing: initial;
      flex: 0 0 auto;
      transition: transform 0.4s;
      span {
        display: block;
        width: 26px;
        height: 2px;
        background: $color-grey;
        opacity: 1;
        transition: opacity 0.2s, transform 0.2s;
        margin-top: 4px;
      }
      span:first-child {
        margin-top: 0;
      }
      &.active {
        transform: rotate(180deg);
        span:first-child {
          transform: translate(0%, 6px) rotate(45deg);
        }
        span:last-child {
          transform: translate(0%, -6px) rotate(-45deg);
        }
        span:nth-child(2) {
          opacity: 0;
        }
      }
    }
  }
}

//
// No mater
//
body {
  height: 100vh;
}
